.contact-container {
    margin: 92px 0;
}

.input-container {
    display: flex;
    flex-direction: column;
    gap: 32px;
    width: 75%;
    margin-top: 12px;
}

input {
    height: 28px;
    padding: 8px;
    font-size: 16px;
}

input:last-child {
    height: 120px;
    padding: 2px 8px;
    margin: 0;
}
.contact-links-container {
    display: flex;
    flex-direction: column;
    gap: 14px;
    margin-left: 14px;
}

.link-row {
    display: flex;
    gap: 22px;
    cursor: pointer;
}

.link-row > a {
    display: flex;
    align-items: center;
    gap: 12px;
}

.link-row  img {
    width: auto;
    height: 50px;
    margin: 6px 0;
}

.link-row:first-child {
    margin-top: 40px;
}

.title {
    display: flex;
    position: relative;
    z-index: 0;
    align-items: center;
    margin-bottom: 32px;
}

.title > h2 {
    margin:0 0 0 25px;
}

.dot {
    position: absolute;
    z-index: -1;
    display: flex;
    height: 70px;
    width: 70px;
    background: linear-gradient( #00BCDC, #05C54F );
    border-radius: 50%;
    margin-bottom: 8px;
  }



/* Formally 1024px */
@media  screen and (max-width: 64em) { 

    .link-row img {
        margin-right: 25px;
    }

}



/* Formally 768px */
@media  screen and (max-width: 48em) {

    .link-row > a {
        gap: 22px;
    }
  }



/* Formally 425px */
@media  screen and (max-width: 26.5625em) {

    .title > h2 {
        margin: 0 0 0 12px;
    }

    .title > h4 {
        margin: 0 0 0 15px;
        font-size: 21px;
    }

    .contact-container h4 {
        font-size: 21px;
    }

    .link-row img {
        width: auto;
        height: 40px;
        margin-right: 0px;
    }

    .link-row  h3 {
        font-size: 16px;
    }

    .contact-links-container {
        margin-left: 5px;
    }

    .dot {
        height: 50px;
        width: 50px;
    }

  }